*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6{
  font-size: 100%;
  font-weight: normal;
}
.wrap{
  height: 100vh;
  position: relative;
  background-color: tomato;
}
.header{
  padding: 1rem;
  background-color: teal;
  color: #fff;
}
.header h1{
  font-size: 2.3rem;
  font-weight: bold;
}

/* 리스트 */
section.list01{
  display: flex;
  justify-content: space-between;
}
section.list{
  border-bottom: 1px solid teal;
  color: #535353;
  padding: 1.5rem 1rem;
}
section.list div{
  color: #fff;
  margin-bottom: 0.5rem;
  font-weight: 600;
}
section.list .nick{
  color: teal;
}
.like{
  cursor: pointer;
}


/* 버튼 */
.btnWrap{
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
}
button{
  margin: 1rem;
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid teal;
  color: teal;
  font-weight: bold;
  cursor: pointer;
}


/* 모달 */
.modal{
  width: 50%;
  padding: 2rem 2.5rem;
  position: absolute;
  border-radius: 30px;
  background-color: rgb(255, 196, 35);
  box-shadow: 0 0 15px 2px rgba(255, 196, 35, 0.5);
  color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.modal h3{
  font-size: 2rem;
  font-weight: bold;
}
.modal p{
  margin-top: 1rem;
}
.modal p:last-child{
  text-align: right;
}